.App {
  text-align: center;
}

.App-logo {
  width: 40%;  
  pointer-events: none;
}

.App-header {  
  background-color: #fff;  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #000;
}

.header {
  width: 100%;
  height: 50vh;
  background-color: #fff;
}
.header img {
  height: 100%;
}

.App-link {
  color: #61dafb;
}

.tw-logo {
  width: 30%;  
  margin-top: 2rem;
  transition: 0.5s;
  opacity: 0.85;
}
.tw-logo:hover {
  transform:scale(1.2);
  opacity: 0.95;
}